import BMap from 'BMap'
export default {
  name: 'Map',
  methods: {
    handleHide () {
      this.$emit('closeMap')
    }
  },
  mounted () {
    var map = new BMap.Map(this.$refs.container)
    var point = new BMap.Point(116.404, 39.915)
    map.centerAndZoom(point, 15)
  }
}



