import CommonHeader from '@/pages/common/header.vue'
import OurMap from '@/pages/contact/Map.vue'
import Rule from '@/pages/contact/Rule.vue'
import Introduce from '@/pages/contact/Introduce.vue'
export default {
  name: 'Contact',
  data () {
    return {
      showMap: false,
      showRule: false,
      showIntroduce: false
    }
  },
  methods: {
    handleShowPhone () {
      this.showPhone = true
    },
    handleRuleShow () {
      this.showRule = true
    },
    handleCloseMap () {
      this.showMap = false
    },
    handleCloseRule () {
      this.showRule = false
    },
    handleIntroduceShow () {
      this.showIntroduce = true
    },
    handleCloseIntroduce () {
      this.showIntroduce = false
    }
  },
  components: {
    CommonHeader,
    OurMap,
    Rule,
    Introduce
  }
}



